.profile{
    display:flex
}
.profileRight{
    flex:9
}

.profileCover{
    height:320px;
    position:relative;

}
.profileCoverImg{
    width:100%;
    height:250px;
    object-fit:cover;

}
.profileUserImg{
    width:150px;
    height:150px;
    border-radius:50%;
    object-fit: cover;
    position:absolute;
    left:0;
    right:0;
    margin:auto;
    top:150px;
    border:3px solid white;



}
.profileRightBottom{
    display:flex
}
.profileInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.profileInfoName{
    margin: 0;
    font-size:24px;

}
.profileInfoDesc{
    margin: 0;
    font-weight:300px;

}